<script setup lang="ts">
import { computed, onBeforeMount, watch } from "vue";
import { storeToRefs } from "pinia";
import { ACol, ASelect, ACheckbox } from "@actindo/core-ui";
import type {
  IDataTypeInstance,
  ICacheEntitiesAttributeInSetCache,
} from "@/services/api/DataHub/Attributes";
import type { ITenantAttributeLanguage } from "@/services/api/DataHub/Languages";
import type { ITenantAttributeScope } from "@/services/api/DataHub/Scopes";
import { useDataTypesStore } from "@/stores/dataTypes";
import type { IDataTypesUnitValueConfiguration } from "./models";

defineProps<{
  dataTypeInstance: IDataTypeInstance<IDataTypesUnitValueConfiguration>;
  remoteScopes?: ITenantAttributeScope[];
  remoteLanguages?: ITenantAttributeLanguage[];
  remoteAttributes?: ICacheEntitiesAttributeInSetCache[];
  readonly?: boolean;
}>();

const configuration = defineModel<IDataTypesUnitValueConfiguration>();

const dataTypesStore = useDataTypesStore();
const { dimensions, isDimensionsLoading } = storeToRefs(dataTypesStore);

const units = computed(() => {
  const dimension = dimensions.value.find(
    ({ id }) => id === configuration.value.dimensionId,
  );
  if (!dimension) {
    return [];
  }

  return dimension.units.map(({ id, name, symbol }) => ({
    id,
    title: `${name} (${symbol})`,
  }));
});

watch(
  () => configuration.value.dimensionId,
  () => {
    configuration.value.defaultUnitId = undefined;
  },
  { immediate: true },
);

onBeforeMount(dataTypesStore.fetchDimensions);
</script>

<template>
  <ACol>
    <ASelect
      v-model="configuration.dimensionId"
      :items="dimensions"
      :loading="isDimensionsLoading"
      :label="$t('datahub-plugin.unitValue.dimension')"
      :readonly="readonly"
      item-title="name"
      item-value="id"
    />
  </ACol>
  <ACol>
    <ASelect
      v-model="configuration.defaultUnitId"
      :items="units"
      :loading="isDimensionsLoading"
      :disabled="!configuration.dimensionId"
      :label="$t('datahub-plugin.unitValue.defaultUnit')"
      :readonly="readonly"
      item-value="id"
    />
  </ACol>
  <ACol>
    <ACheckbox
      v-model="configuration.readOnly"
      :label="$t('datahub-plugin.stringValue.readOnly')"
      :readonly="readonly"
    />
  </ACol>
</template>
